@font-face {
  font-family: 'icomoon';
  src:  url('./icomoon.eot?u4elr4');
  src:  url('./icomoon.eot?u4elr4#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?u4elr4') format('truetype'),
    url('./icomoon.woff?u4elr4') format('woff'),
    url('./icomoon.svg?u4elr4#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-options:before {
  content: "\e900";
}
.icon-delete:before {
  content: "\e901";
}
.icon-download:before {
  content: "\e902";
}
.icon-print:before {
  content: "\e903";
}
.icon-bylaws:before {
  content: "\e904";
}
